import React from 'react';
import { Link } from 'react-router'

import myOrder from './../../../images/user/mc_01.png';
import allOrder from './../../../images/user/arrow_right_b.png';
import payment from './../../../images/user/order-dzf.png';
import takegoods from './../../../images/user/order-dsh.png';
import complate from './../../../images/user/order-dpj.png';
import refund from './../../../images/user/order-thh.png';

import inte_center from './../../../images/user/quan.png';

import shop_collect from './../../../images/user/i_1.png';
import store_collect from './../../../images/user/i_2.png';
import heel from './../../../images/user/i_3.png';
import back_info from './../../../images/user/i_4.png';
import my_asset from './../../../images/user/mc_02.png';
import consigeen from './../../../images/user/mc_03.png';
import invite from './../../../images/user/mc_08.png';
import my_invite from './../../../images/user/mc_09.png';
import my_monry from './../../../images/user/i_5.png';
import voucher from './../../../images/user/i_6.png';
import packed from './../../../images/user/i_7.png';
import sign_in from './../../../images/user/i_8.png';

import set from './../../../images/user/mc_04.png';
import loca from './../../../images/user/mc_07.png';
import help from './../../../images/user/mc_01.png';


class User extends React.Component{
	render(){
		return (
			<div className="m-user">
				<div className="head">
					<Link to="/login" className="login-box ani elastic-in-down">
						<div className="login"></div>
						<p></p>
					</Link>
				</div>
				<div className="myfom">
					<div className="order">
						<a href="#" className="myOrder"><img src={myOrder} /> </a>
						<a href="#" className="allOrder"> <img src={allOrder} /></a>
					</div>
					<ul className="wait">
						<li><a href="#"><img src={payment} /><span></span></a></li>
						<li><a href="#"><img src={takegoods} /><span></span></a></li>
						<li><a href="#"><img src={complate} /><span></span></a></li>
						<li><a href="#"><img src={refund} /><span>/</span></a></li>
					</ul>
				</div>
				<div className="integral">
					<a href="#" className="integral-left">
						<img src={inte_center} />
						<p></p>
					</a>
					<div className="integral-middle">
						<a href="#" className="shop-inte">
							<p className="look"></p>
							<p></p>
						</a>
						<a href="#" className="n-inte">
							<p className="look">N</p>
							<p></p>
						</a>
					</div>
					<div className="integral-right">
						<a href="#" className="logo-inte">
							<p className="look"></p>
							<p></p>
						</a>
						<a href="#" className="store-inte">
							<p className="look"></p>
							<p></p>
						</a>
					</div>
				</div>
				
				<div className="menu">
					<a href="#" className=""><img src={shop_collect} /><span></span></a>
					<a href="#" className=""><img src={store_collect} /><span></span></a>
					<a href="#" className=""><img src={heel} /><span></span></a>
					<a href="#" className="back_info"><img src={back_info} /><span></span></a>
					<a href="#" className=""><img src={my_asset} /><span></span></a>
					<a href="#" className=""><img src={consigeen} /><span></span></a>
					<a href="#" className=""><img src={invite} /><span></span></a>
					<a href="#" className="my_invite"><img src={my_invite} /><span></span></a>
					<a href="#" className="my_monry"><img src={my_monry} /><span></span></a>
					<a href="#" className="voucher"><img src={voucher} /><span></span></a>
					<a href="#" className="packed"><img src={packed} /><span></span></a>
					<a href="#" className="sign_in"><img src={sign_in} /><span></span></a>
				</div>
				<div className="set">
					<div>
						<img src={set} />
						<span></span>
					</div>
					<img src={allOrder} />
				</div>
				<div className="loca">
					<div>
						<img src={loca} />
						<span></span>
					</div>
					<img src={allOrder} />
				</div>
				<div className="help">
					<div>
						<img src={help} />
						<span></span>
					</div>
					<img src={allOrder} />
				</div>
			</div>
		)
	}
}

export default User;


